import React, { useEffect, useState } from "react";
// react plugin used to create charts
// import { Line, Pie } from "react-chartjs-2";
// reactstrap components
import {
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  CardTitle,
  CardText,
  Row,
  Col,
  Button,
} from "reactstrap";
import { app } from "../database/firebase";

const db = app.firestore();

// core components
// import {
//   dashboard24HoursPerformanceChart,
//   dashboardEmailStatisticsChart,
//   dashboardNASDAQChart,
// } from "variables/charts.js";

const Dashboard = (props) => {
  const [users, setUsers] = useState([]);
  const [all, setAll] = useState([]);
  useEffect(() => {
    const fetchUsers = async () => {
      const usersCollection = await db.collection("users").get();
      setUsers(
        usersCollection.docs.map((doc) => {
          return { id: doc.id, ...doc.data() };
        })
      );
    };
    fetchUsers();
  }, []);
  useEffect(() => {
    const fetchCampaign = async () => {
      if (users.length > 0) {
        await users.forEach(async (user) => {
          const dbRef = await db
            .collection("campaigns")
            .where("id", "==", user.campaign);
          const campData = await dbRef.get();
          if (campData?.docs[0]) {
            const allData = await campData.docs[0]
              .data()
              .stores.reduce((results, store) => {
                if (store.id === user.store) {
                  results.push({
                    user,
                    campaign: campData.docs[0].data(),
                    store,
                  });
                }
                return results;
              }, []);
            setAll((oldArray) => [...oldArray, ...allData]);
          }
        });
      }
    };
    fetchCampaign();
  }, [users]);


  return (
    <>
      <div className="content">
        <Row>
          {all.map((val) => (
            <Col sm={3}>
              <Card>
                <CardBody>
                  <CardTitle tag="h6">Campaña</CardTitle>
                  <CardText tag="h4">{val?.campaign.name}</CardText>
                  <CardText>{val?.user.name}</CardText>
                  <CardText>Fecha de ingreso : {val?.user.create_at}</CardText>
                  <CardText>Tienda : {val?.store.name}</CardText>
                </CardBody>
              </Card>
            </Col>
          ))}
        </Row>
      </div>
    </>
  );
};

export default Dashboard;
