import React, { useState } from "react";

import { Card, Col, Form, FormGroup, Button, Input, Label } from "reactstrap";

import crypto from "crypto";
const Login = (props) => {
  const [admin] = useState({ email: "admin@gmail.com", password: "12345678" });
  const [user, setUser] = useState({
    email: "",
    password: "",
  });

  const onSubmit = (event) => {
    const { history } = props;
    const token = crypto.randomBytes(20).toString("hex");
    event.preventDefault();
    if (admin.email === user.email && admin.password === user.password) {
      localStorage.setItem("token", token);
      history.push("admin/campaigns");
    }
  };

  return (
    <>
      <div className="content justify-content-center d-flex p-5">
        <Col sm="4">
          <Card className="w-100 p-3">
            <Form>
              <FormGroup>
                <Label for="exampleInputEmail1">CORREO</Label>
                <Input
                  type="email"
                  className="form-control"
                  onChange={(event) =>
                    setUser({ ...user, email: event.target.value })
                  }
                />
              </FormGroup>
              <FormGroup>
                <Label for="exampleInputPassword1">CONTRASEÑA</Label>
                <Input
                  type="password"
                  onChange={(event) =>
                    setUser({ ...user, password: event.target.value })
                  }
                />
              </FormGroup>
              <Button
                type="submit"
                className="btn btn-primary w-100"
                onClick={(event) => onSubmit(event)}
              >
                Ingresar
              </Button>
            </Form>
          </Card>
        </Col>
      </div>
    </>
  );
};

export default Login;
